
.button {
    background-color: white;
    border: 2px solid black;
    color: black;
    padding: 2px 2px;
    border-radius: 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 12px;
    transition-duration: 0.4s;
    width: 120px;
    margin: 2px;
}

.button:hover {
    background-color: #e7e7e7;
    color: black;
  }

.table{
  border: 0px solid black;
  border-spacing: 2px;
  /* border-collapse: collapse; */
}

.blink {
  animation: blink 1s infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
  
}

@keyframes cfbottom {
  0% {
    opacity: 0;
  }
  67% {
    opacity: 1;
  }
  99% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes cfmiddle {
  0% {
    opacity: 0;
  }
  33% {
    opacity: 1;
  }
  66% {
    opacity: 1;
  }
  67% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes cftop {
  0% {
    opacity: 1;
  }
  33% {
    opacity: 1;
  }
  34% {
    opacity: 0;
  }
  99% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.cf {
  position: relative;
  width: 20px;
  height: 40px;
  margin: 0 auto;
}

.cf .img {
  position: absolute;
  width: 20px;
  height: 40px;
  left: 0;
  /* -webkit-transition: opacity 1500ms ease-in-out;
  -moz-transition: opacity 1500ms ease-in-out;
  -o-transition: opacity 1500ms ease-in-out; */
  /* transition: opacity 1s ease-in-out; */
  
}

.cf .img.bottom {
  animation-name: cfbottom;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-duration: 1s;
  animation-direction: normal;
}

.cf .img.middle {
  animation-name: cfmiddle;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-duration: 1s;
  animation-direction: normal;
}

.cf .img.top {
  animation-name: cftop;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-duration: 1s;
  animation-direction: normal;
}




